@fill-tap : rgba((17,17,17,0.1));
.ant-drawer.ant-drawer-open .ant-drawer-content {
  position: relative;
  z-index: 1;
  overflow: auto;
  background-color: #fff;
  background-clip: padding-box;
  border: 0;
}
.ant-drawer.ant-drawer-open.ant-drawer-right .ant-drawer-content {
  border-top-left-radius: 12px;
  border-bottom-left-radius: 12px;
}
.ant-drawer.ant-drawer-open.ant-drawer-bottom .ant-drawer-content {
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
}
.ant-drawer.ant-drawer-open.ant-drawer-left .ant-drawer-content {
  border-top-right-radius: 12px;
  border-bottom-right-radius: 12px;
}
.ant-drawer.ant-drawer-open.ant-drawer-top .ant-drawer-content {
  border-bottom-left-radius: 12px;
  border-bottom-right-radius: 12px;
}
.am-modal-wrap,
.am-modal-wrap.am-modal-wrap-popup {
  z-index: 999999;
}
.am-modal-mask {
  z-index: 9999;
}
span.ys-smart-search-item {
  display: block;
  width: 100%;
  font-size: 0.32rem;
  padding: 0 0.24rem;
  color: #555;
  height: 0.88rem;
  line-height: 0.88rem;
  position: relative;
}
span.ys-smart-search-item::after {
  content: '';
  display: block;
  position: absolute;
  width: calc(100% - 12*0.02rem);
  height: 0.02rem;
  background-color: #e9e9e9;
  transform: scaleY(0.5);
  bottom: 0;
  right: 0;
}
span.ys-smart-search-item:last-child::after {
  display: none;
}
.ys-refer-smart-search {
  display: flex;
  flex-direction: column;
  position: fixed;
  left: 0px;
  right: 0px;
  z-index: 99999;
  background: #ffffff;
}
.ys-refer-smart-search > div.ys-history-select,
.ys-refer-smart-search > div.ys-empty-container {
  height: 100%;
  flex: 1;
}
.ys-refer-smart-search .am-list-body {
  border: none;
  border-bottom: none;
  border-top: none;
}
.ys-refer-smart-search .am-list-body::before,
.ys-refer-smart-search .am-list-body::after {
  display: none !important;
}
.ys-refer-smart-search .am-list-view-scrollview-content.am-list {
  width: 100%;
}
.ys-refer-smart-search .am-list-view-scrollview-content.am-list .ys-smart-search-item {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
.ys-refer-smart-search .am-result::after {
  display: none !important;
}
.ys-refer-smart-search .am-list-view-scrollview {
  width: 100%;
  height: 100%;
}
.ys-refer-smart-search .ys-refer-smart-search-botton {
  display: flex;
  align-items: center;
  font-size: 0.3rem;
  position: relative;
  height: 1rem !important;
}
.ys-refer-smart-search .ys-refer-smart-search-botton::before {
  content: '';
  display: block;
  position: absolute;
  height: 0.02rem;
  width: 100%;
  background-color: #e9e9e9;
  transform: scaleY(0.5);
  top: 0;
  right: 0;
}
.ys-refer-smart-search .ys-refer-smart-search-botton span {
  flex: 1;
}
.ys-refer-smart-search .ys-refer-smart-search-botton .cancel {
  float: left;
  color: #111;
  margin-left: 0.26rem;
}
.ys-refer-smart-search .ys-refer-smart-search-botton .confirm {
  float: right;
  color: #e14c46;
  margin-right: 0.26rem;
  text-align: right;
}
.ys-smart-search-warp p,
.ys-history-select p {
  margin: 0!important;
  padding: 0;
  font-size: 0.32rem;
  color: #111;
  height: 0.88rem;
  padding: 0 0.24rem;
  line-height: 0.88rem;
  position: relative;
}
.ys-smart-search-warp p.ys-smart-search-p::after,
.ys-history-select p.ys-smart-search-p::after {
  content: '';
  display: block;
  position: absolute;
  height: 0.02rem;
  background-color: #e9e9e9;
  transform: scaleY(0.5);
  bottom: 0;
  left: 0.24rem;
  right: 0;
}
.ys-smart-search-warp p .ys-empty-right,
.ys-history-select p .ys-empty-right {
  float: right;
  color: #a0a0a0;
  font-size: 0.3rem;
  display: flex;
}
.ys-smart-search-warp p .ys-empty-right i.smart-cancel-up,
.ys-history-select p .ys-empty-right i.smart-cancel-up {
  width: 0.36rem;
  display: inline-block;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 0.32rem;
  background-image: url(data:image/png;base64,);
}
.ys-history-select .am-list-footer {
  float: right;
  padding: 0;
  margin: 0.12rem 0.24rem 0.12rem 0;
}
.ys-history-select .ys-empty-right {
  float: right;
  color: #a0a0a0;
  font-size: 0.3rem;
  display: flex;
}
.ys-history-select .ys-empty-right i {
  width: 0.36rem;
  height: 0.64rem;
  display: inline-block;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 0.36rem;
  background-image: url(data:image/png;base64,);
}
.ys-history-select .list-view-section-body {
  padding: 0 0.24rem;
}
.ys-history-select .list-view-section-body .ys-smart-search-item {
  font-size: 0.3rem;
  padding: 0 0.42rem;
  margin: 0.12rem 0.16rem 0.12rem 0;
  color: #555;
  height: 0.64rem;
  line-height: 0.64rem;
  background: #f5f5f5;
  border-radius: 0.36rem;
  width: auto;
  float: left;
}
.ys-history-select .list-view-section-body .ys-smart-search-item::after {
  display: none!important;
}
